.projects {
  width: 100%;
  gap: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  margin-top: 20px;
}

.project {
  padding: 10px;
  border-radius: 5px;
  width: 100%;
  transition: all .2s;
  /*  */
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.project:hover{
  background: var(--project-hvr);
}

.project:hover img{
  transform: scale(1.05);
}

.project__image {
  box-shadow: 1px 1px 5px #22222243;
  width: 100%;
  position: relative;
  padding-bottom: 56%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  overflow: hidden;
}

.project__image > img{
  position: absolute;
  top: 0;
  transition: all .3s;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.project__image > img:last-child{
  display: none;
}

.project__description {
  width: 100%;
  padding: 10px 0;
}

.project__description > span{
  font-size: 15px;
  color: var(--txt-color);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  text-align: start;
  overflow: hidden;
  word-break: break-word;
}

.project__technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  width: 100%;
}

.project__technologies > span{
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
  font-size: 18px;
  color: var(--txt-color);
  background: var(--project-tech-bg);
  color: var(--project-tech-color);
}

.project__links{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  width: 100%;
}

.project__links > a{
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background: #159cd5;
  font-size: 20px;
  margin-top: 10px;
  color: #fff;
  border-radius: 5px;
  text-decoration: none;
  transition: all .3s;
}

.project__links > a:hover{
  filter: brightness(.9);
}


@media screen and (max-width: 768px){
  .project__image > img:last-child{
    display: block;
  }

  .project__image > img:first-child{
    display: none;
  }
} 

@media screen and (max-width: 470px) {
  .projects {
    grid-template-columns: 1fr;
  }
}