footer {
  padding: 100px 10px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

footer > p {
  font-weight: bold;
  color: var(--txt-color);
}

footer > a{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  color: var(--txt-color);
  text-decoration: none;
}

footer > a > svg{
  font-size: 25px;
}

footer > p > a {
  color: var(--txt-color);
}
footer > div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: var(--txt-color);
}

footer > div > a {
  font-size: 30px;
  color: var(--txt-color);
}

footer > div > a:hover,
footer > p > a:hover {
  opacity: 0.85;
}
