header {
  background: var(--navbar-bg);
  position: sticky;
  top: 0;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

header > nav {
  padding: 10px 20px;
  margin: 0 auto;
  max-width: 1300px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

header > nav > .nav__logo > a {
  font-size: 25px;
  text-decoration: none;
  color: var(--txt-color);
}

header > nav > .nav__links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  list-style: none;
}

header > nav > .nav__links a {
  color: var(--txt-color);
  text-decoration: none;
  position: relative;
  display: inline-block;
}

header > nav > .nav__links a::after{
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  height: 2px;
  background: var(--txt-color);
  width: 0%;
  transition: all .2s;
  border-radius: 5px;
  pointer-events: none;
}

header > nav > .nav__links a:hover::after{
  width: 100%;
}

header > nav > .nav__hamburger {
  display: none;
}

/* Theme Btn */
.btnTheme {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  font-size: 20px;
  margin-left: 20px;
  background: transparent;
  color: var(--txt-color);
}

.btnTheme:hover {
  background: var(--btnTheme-hvr-bg);
}

/* btn hamburguer */
.btnHamburger {
  outline: none;
  border: none;
  color: var(--txt-color);
  background: transparent;
  font-size: 35px;
  cursor: pointer;
}

.menu {
  display: none;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  justify-content: start;
  align-items: end;
  width: 70%;
  height: 100vh;
  background: var(--body-bg);
  position: fixed;
  top: 0;
  right: 0;
  transition: right 0.5s;
  z-index: 3000;
  box-shadow: 0 0 10px #2222223b;
  overflow: hidden auto;
}

.menu div {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 10px;
  width: 100%;
}

.menu div ul {
  list-style: none;
}

.menu div ul li a {
  display: block;
  margin: 15px 0;
  text-decoration: none;
  color: var(--txt-color);
  font-size: 20px;
}

.showMenu {
  right: 0;
}

.hideMenu {
  right: -100%;
}

/* root when menu is active styles */
.brightnessLow {
  filter: none;
  transition: all 0.3s;
}

.nopointerevents{
  pointer-events: all;
}

@media screen and (max-width: 768px) {
  header > nav > .nav__hamburger {
    display: block;
  }

  header > nav > .nav__links {
    display: none;
  }

  /*  */
  .brightnessLow {
    filter: brightness(0.6);
  }

  .nopointerevents{
    pointer-events: none;
  }

  .menu {
    display: flex;
  }

  .btnTheme {
    margin-left: 0;
  }
}
