form{
    width: 100%;
    max-width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    margin: 30px auto 0;
    /* outline: 1px solid black; */
}

form > .form-group{
    width: 100%;
    position: relative;
}

form > .form-group > input{
    padding: 12px;
    width: 100%;
    border-radius: 5px;
    height: 50px;
    outline: 1px solid var(--txt-color);
    border: none;
    font-size: 17px;
    background: transparent;
}

form > .form-group > label{
    pointer-events: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: all .1s;
    background: var(--body-bg);
    padding: 5px;
    left: 6px;
    font-size: 17px;
    opacity: .6;
    color: var(--txt-color);
}

form > .form-group > textarea::placeholder{
    opacity: .6;
    color: var(--txt-color);
}

form > .form-group > textarea{
    padding: 10px;
    width: 100%;
    border-radius: 5px;
    outline: 1px solid var(--txt-color);
    height: 200px;
    border: none;
    font-size: 17px;
    resize: none;
    background: transparent;
}

form > .form-group > input:focus + label,
form > .form-group > input:not(:placeholder-shown) + label{
    top: -1px;
    left: 7px;
    font-size: 14px;
    opacity: 1;
}

form > button{
    padding: 15px;
    cursor: pointer;
    font-weight: bold;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    border-radius: 5px;
    border: none;
    outline: none;
    color: var(--btn-form-color);
    background: var(--btn-form-bg);
}

form > button > svg{
    font-size: 25px;
    transition: all .3s;
}

form > button:hover > svg{
    transform: translate(3px, -3px);
}

form > button:hover{
    filter: brightness(.8);
}

form > button:active{
    transform: scale(.98);
}

.formErr{
    padding: 15px;
    font-size: 16px;
    font-weight: bold;
    background: #ff1e1e;
    color: #fff;
    border-radius: 5px;
    text-align: center;
}