/* Light Theme */
:root{
  --body-bg: #fff;
  --navbar-bg: #ffffffd9;
  --txt-color: #000;
  --title-color: #000;
  --btnTheme-hvr-bg: #0000000e;
  --btn-form-bg: #222;
  --btn-form-color: #dadce1;
  --project-tech-bg: #cecece;
  --project-tech-color: #222;
  --project-hvr: #e0e0e0;
  color-scheme: light;
}

/* Dark Theme */
.dark-theme{
  --body-bg: #18191a;
  --navbar-bg: #242526d7;
  --txt-color: #dadce1;
  --title-color: #dadce1;
  --btnTheme-hvr-bg: #464646;
  --btn-form-bg: #b0b0b0;
  --btn-form-color: #222;
  --project-tech-bg: #5e5e5e;
  --project-tech-color: #f8f8f8;
  --project-hvr: #2d2d2d;
  color-scheme: dark;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body, #root{
  background: var(--body-bg);
  transition: all 0s;
}

footer,
section{
  width: 100%;
  max-width: 1300px;
  padding: 30px 20px;
  margin: 0 auto;
}

button *{
  pointer-events: none;
}