.section-1{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    align-items: flex-start;
}

.section-1 > div{
    width: 100%;
}

.section-1 p{
    margin-top: 20px;
    line-height: 30px;
}

.section-1__technologies > div{
    margin-top: 20px;
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.section-1__technologies > div > span{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    padding: 10px;
    border-radius: 5px;
    font-weight: bold;
    transition: all .2s;
    cursor: default;
}

.section-1__technologies > div > span:hover{
    transform: scale(1.1);
}

.section-1__technologies > div > span > svg{
    font-size: 27px;
}

.html{
    background: #ea612b;
}

.css{
    background: #159cd5;
}

.js{
    background: #f7d723;
}

.bs{
    background: #8c65e2;
}

.tailwind{
    background: #37b3e9;
}

.react{
    background: #1bc3e0;
}

.ts{
    background: #0276c6;
}

.nodejs{
    background: #84c03b;
}

.express{
    background: #2a2a2a;
    color: #fff;
}

.mongodb{
    background: #459d45;
}





@media screen and (max-width: 768px){
    .section-1{
        grid-template-columns: 1fr;
        gap: 50px;
    }    
}